<template>
    <canvas :id="chartName" width="400" height="300"></canvas> 
</template>

<script>

import Chart from 'chart.js';

export default {
    name: 'Histogram',
    props: {
        input: Array,
        inputLabel: Array,
        chartName: String
    },
    data () {
        return{
            mainChart: null
        };
    },
    watch: {
        input: function () {
            this.mainChart.data.labels = this.inputLabel;
            this.mainChart.data.datasets[0].data = this.input;
            this.mainChart.options = {
                scales: {
                    xAxes: [{
                        display: false,
                        barPercentage: 1.3,
                        ticks: {
                        max: this.input.length,
                    }
                    }, {
                        display: false,
                        ticks: {
                            autoSkip: false,
                            max: this.input.length+1,
                        }
                    }],
                    yAxes: [{
                        ticks: {
                        beginAtZero:true
                        }
                    }]
                }
            }
            
            this.mainChart.update();
            console.log(this.input);
        }
    },
    mounted() {
        this.init();
    },
    methods: {
        init() {
            var dataValues = this.input;
            var dataLabels = this.inputLabel;
            console.log(dataValues);
            console.log(dataLabels);
            this.mainChart =  new Chart(document.getElementById(this.chartName), {
                type: 'bar',
                data: {
                    labels: dataLabels,
                    datasets: [{
                    label: 'Histogram',
                    data: dataValues,
                    backgroundColor: 'rgba(96, 197, 245, 1)',
                    }]
                },
                options: {
                    scales: {
                        xAxes: [{
                            display: false,
                            barPercentage: 1.3,
                            ticks: {
                            max: this.input.length,
                        }
                        }, {
                            display: false,
                            ticks: {
                                autoSkip: false,
                                max: this.input.length+1,
                            }
                        }],
                        yAxes: [{
                            ticks: {
                            beginAtZero:true
                            }
                        }]
                    }
                }
            });
        }
    }
}
</script>

