<template>
    <div> 
        <canvas :id="chartName" width="400" height="300"></canvas> 
    </div>
</template>

<script>

import Chart from 'chart.js';

export default {
    name: 'Scatterplot',
    props: {
        input: Array,
        chartName: String
    },
    data() {
        return {
            mainChart: null
        };
    },
    watch: {
        input: function () {
            this.mainChart.data.datasets[0].label = 'Random Size '+this.input.length;
            this.mainChart.data.datasets[0].data = this.input;
            this.mainChart.update();
        }
    },
    mounted() {
        this.init();
    },
    methods: {
        init() {
            this.mainChart = new Chart(document.getElementById(this.chartName), {
                type: 'scatter',
                data: {
                    datasets: [{
                        label: 'Random Size '+this.input.length,
                        data: this.input
                    }]
                },
                options: {
                    scales: {
                        xAxes: [{
                            type: 'linear',
                            position: 'bottom'
                        }]
                    }
                }
            });
        }
    }
}
</script>