<template>
    <div id="app">
        <div class="container">
            <div class="row">
                <div class="col-sm-12 jumbotron">
                    <h2>Spatial Distance Histogram (SDH)</h2>
                    <p> An SDH is the histogram of distances between all pairs of particles in the system and it represents a discrete approximation of the continuous probability distribution of distances named Radial Distribution Function (RDF).  </p>
                </div>
            </div>
            <div class="row">
                <div class="col-sm-5">
                    <div class="row">
                        <h2> Configuration </h2>
                    </div>
                    <div class="row">
                        <label for="customRange1"> Number of Particles: {{inputSize}}</label>
                        <input type="range" v-model="inputSize" class="custom-range" id="customRange1" min="1" max="5000" v-on:change="changeSize">
                        <label for="customRange1"> Bucket Size: {{bucketsize}}</label>
                        <input type="range" v-model="bucketsize" class="custom-range" id="customRange2" min="100" max="1000" v-on:change="changeBinSize">
                    </div>
                    <br>
                    <div class="row">
                        <h2> Particles </h2>
                        <Scatterplot v-bind:input="this.Atom" chartName='scatterPlot1'></Scatterplot>
                    </div>
                </div>

                <div class="col-sm-7">
                    <h2> Historgram </h2>
                    <Spinner size="large" id="loading" message="Loading..."></Spinner>
                    <Histogram v-bind:input="this.hist" v-bind:inputLabel="this.histLabel" chartName='histogramPlot'></Histogram>
                </div>
            </div>
            
        </div>
    </div>
</template>

<script>
import Histogram from './components/Histogram.vue'
import Scatterplot from './components/Scatterplot.vue'
import Spinner from 'vue-simple-spinner'

export default {
    name: 'App',
    components: {
        Histogram,
        Scatterplot,
        Spinner
    },
    data() {
        return {
            priceV2: [75.0,200.0,100.0,200.0],
            inputSize: 100,
            Atom: [],
            hist: [],
            histLabel: [],
            buckets: 0,
            bucketsize: 500 
        };
    },
    mounted() {
        this.init();
        this.changeSize();
        this.sdh();
    },
    methods: {
        init() {
            document.title = "AppSDH"
        },
        sdh() {
            this.computeBuckets();
            this.hist = new Array(this.buckets);
            this.histLabel = new Array(this.buckets);
            var i,j;
            for(i=0;i<this.hist.length;i++) {
                this.hist[i] = 0;
                this.histLabel[i] = i;
            } 
            for(i=0;i<this.Atom.length;i++) {
                for(j=i+1;j<this.Atom.length;j++){
                    var diffX = Math.pow(this.Atom[i].x-this.Atom[j].x,2);
                    var diffY = Math.pow(this.Atom[i].y-this.Atom[j].y,2);
                    var dist = Math.sqrt(diffX+diffY);
                    var index = dist/this.bucketsize;
                    index = parseInt(index,10);
                    this.hist[index]++;
                }
            }
            document.getElementById("loading").style.display = "none";
            document.getElementById("histogramPlot").style.display = "block"; 
        },
        computeBuckets () {
            var xmin, ymin, xmax, ymax=0.0;
            xmin = ymin =  Number.MAX_SAFE_INTEGER;
            xmax = ymax = 0.0;
            var i;
            for(  i = 0; i < this.Atom.length; i++ ) {
                if(this.Atom[i].x < xmin) xmin = this.Atom[i].x;
                if(this.Atom[i].y< ymin) ymin = this.Atom[i].y;
                if(this.Atom[i].x > xmax) xmax = this.Atom[i].x;
                if(this.Atom[i].y > ymax) ymax = this.Atom[i].y;
            }
            var dist = Math.sqrt( Math.pow(xmax-xmin,2) + Math.pow(ymax-ymin,2) );
            this.buckets =  parseInt(dist / this.bucketsize + 1,10);
        },
        changeSize() {
            this.Atom = [];
            var i=0;
            for(i=0;i<this.inputSize;i++) {
                this.Atom.push({x:Math.floor(Math.random()*100000),y:Math.floor(Math.random()*100000)});
            }
            document.getElementById("histogramPlot").style.display = "none";
            document.getElementById("loading").style.display = "block";    
            setTimeout(this.sdh, 0);
        },
        changeBinSize() {
            document.getElementById("histogramPlot").style.display = "none";
            document.getElementById("loading").style.display = "block";
            setTimeout(this.sdh, 0);
        }
    }
}
</script>

